import React from 'react';

const Navigation = ({onRouteChange,isSignedIn}) =>{
	console.log("Navigation isSignedIn: ",isSignedIn);
	if(isSignedIn)
	{
		return(
			<nav style={{display: 'flex', justifyContent:'flex-end'}}>
			<p onClick={()=>onRouteChange('signout')}  className='f3 link dim black underline pa3 pointer'>Sign Out</p>
			</nav>
		);

	}else{
		return(
			<nav style={{display: 'flex', justifyContent:'flex-end'}}>
			<p onClick={()=>onRouteChange('register')} id='Register' className='f3 link dim black underline pa3 pointer'>Register</p>
			<p onClick={()=>onRouteChange('signin')} id='Signin' className='f3 link dim black underline pa3 pointer'>Sign In</p>
			</nav>
		);	
	}
}
export default Navigation;